.adder{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    width: 90%;
    height: 35px;
    line-height: 35px;
    margin: auto;
    border: 1px solid #ddd;
    border-radius: 5px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 5px 0;
    z-index: 200;
    /* margin-bottom: 20px; */
}
.adder .Addinput{
    outline: none;
    border: none;
    font-family: 'Raleway', sans-serif;
    width: 90%;
    line-height: 30px;
    background: transparent;
    color: inherit;
}
.adder i{
    cursor: pointer;
}


