.task {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    min-height: 40px;
    padding: 10px 0;
    border-bottom: 1px solid #eee;
    position: relative;
}

.task .text::first-letter {
    text-transform: uppercase;
}

.task .text, .task i {
    cursor: pointer;
}

.task .text {
    overflow-wrap: break-word;
}


